.Home-container {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
  height: 100%;
  height: 100vh;
}

.Home-inner-container {
  display: -ms-flexbox;
  display: flex;
  border-radius: 5px;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
  height: 200px;
  width: 300;
  border: 1px solid #7C9885;
}

.Home-links {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
      flex-direction: row;
}

.Home-links li {
  margin: 5px;
}


a {
  text-decoration: none;
}

/* unvisited link */
a:link {
    color: #FB9902;
}

/* visited link */
a:visited {
    color: #2A0934;
}

/* mouse over link */
a:hover {
    color: #FDA623;
}

/* selected link */
a:active {
    color: blue;
  }



